<template>
  <div class="app">
    <div id="canvas"></div>
    <div class="progress"
         v-if="showProgress"
    >
      <vue-circle
        ref="myUniqueID"
        :progress="0"
        :size="150"
        :reverse="false"
        line-cap="round"
        :fill="fill"
        empty-fill="rgba(0, 0, 0, .1)"
        :animation-start-value="0.0"
        :start-angle="0"
        insert-mode="append"
        :thickness="4"
        :show-percent="true"
      >
      </vue-circle>
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { STLLoader } from 'three/examples/jsm/loaders/STLLoader.js';
import { RGBELoader } from "three/examples/jsm/loaders/RGBELoader";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
import VueCircle from "vue2-circle-progress";

export default {
  props: ['sfdc_impression_id'],
  name: "STLviwer",
  components: {  
      VueCircle
  },
  data: function() {
    return {
      scene: null,
      renderer: null,
      camera: null,
      controls: null,
      points: [],
      height: 0,
      object: null,
      mouse: null,
      showProgress:true,
      fill: { gradient: ["#ffffff", "#ffffff", "#ffffff"] },
    };
  },
  methods: {
    async init() {
      var self = this;
        var container = document.getElementById("canvas");      
        this.renderer = new THREE.WebGLRenderer({
          clearColor: 0x000000,
          clearAlpha: 1
        });

        var width = window.innerWidth;
        var height = window.innerHeight;
        this.renderer.setSize(width, height);
        this.renderer.setClearColor(0xffffff, 1);
        container.appendChild(this.renderer.domElement);

        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(0x262626);

        var loader = new STLLoader();
        const ambientLight = new THREE.AmbientLight( 0x101010 );
        this.scene.add( ambientLight );

        const light0 = new THREE.PointLight( 0xffffff, 1, 0 );
        light0.position.set( -200, 0, -100 );
        this.scene.add( light0 );

        const light1 = new THREE.PointLight( 0xffffff, 1, 0 );
        light1.position.set(  200, 0, 100 );
        this.scene.add( light1 );

        const light2 = new THREE.PointLight( 0xffffff, 1, 0 );
        light2.position.set( 0, 200, 100 );
        this.scene.add( light2 );

        const light3 = new THREE.PointLight( 0xffffff, 1, 0 );
        light3.position.set( 0, -200, -100 );
        this.scene.add( light3 );
      const hdrEquirect = new RGBELoader().load(
        "/static/empty_warehouse_01_2k.hdr",  
        () => { 
          hdrEquirect.mapping = THREE.EquirectangularReflectionMapping; 
        }
      );
        let modelid = window.location.href.split("/")[4]
        const manager = new THREE.LoadingManager();
        let mesh1, mesh2
        manager.onStart = function ( url, itemsLoaded, itemsTotal ) {
          console.log( 'Started loading file: ' + url + '.\nLoaded ' + itemsLoaded + ' of ' + itemsTotal + ' files.' );
        };

        manager.onLoad = function ( ) {
          self.scene.add(mesh1);
          self.scene.add(mesh2);
          self.showProgress = false
        };
        manager.onProgress = function (itemsLoaded, itemsTotal) {
         self.$refs.myUniqueID.updateProgress(itemsTotal / 2 * 100);
        };

        manager.onError = function ( url ) {
          console.log( 'There was an error loading ' + url );
        };
        var loader = new STLLoader(manager);
        loader.load(`https://s3.ap-northeast-1.amazonaws.com/static-dev.oh-my-teeth.com/${modelid}/lowerJaw.stl`, function (geometry){
          const material = new THREE.MeshPhysicalMaterial({
            color: 0x02556f,
            transmission: 0,
            roughness: 0,
            envMapIntensity: .1,
            envMap: hdrEquirect
          });
					mesh1 = new THREE.Mesh(geometry, material );
          mesh1.position.set(0, 0, 0);
          mesh1.rotation.x = -Math.PI / 2;
          mesh1.rotation.z = -Math.PI / 2 - 0.5;
          mesh1.name = "lower";
					
				});
        loader.load(`https://s3.ap-northeast-1.amazonaws.com/static-dev.oh-my-teeth.com/${modelid}/upperJaw.stl`, function (geometry){
          const material = new THREE.MeshPhysicalMaterial({
            color: 0x02556f,
            transmission: 0,
            roughness: 0,
            envMapIntensity: .1,
            envMap: hdrEquirect
          });
					mesh2 = new THREE.Mesh(geometry, material );
          mesh2.position.set(0, 0, 0);
          mesh2.rotation.x = -Math.PI / 2;
          mesh2.rotation.z = -Math.PI / 2 - 0.5;
          mesh2.name = "upper";
					
				}); 

        this.points.push(new THREE.Vector3(0, 0, 0));
        this.camera = new THREE.PerspectiveCamera(30, width / height, 1, 2000);
        this.camera.position.y = 0;
        this.camera.position.z = 0;
        this.camera.position.x = -250;
        this.camera.lookAt(new THREE.Vector3(0, 0, 0));
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.BasicShadowMap;
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    },
    animate() {
      this.controls.update();
      this.controls.autoRotate = true;
      requestAnimationFrame(this.animate);
      this.renderer.render(this.scene, this.camera);
    }
  },
  async mounted() {
    await this.init();
    this.animate();
    this.controls.addEventListener( "change", event => {
      var upper = this.scene.getObjectByName("upper");
      var lower = this.scene.getObjectByName("lower");
      if(upper){
        if(this.controls.object.position.y> 245){
          upper.visible = false
        }
        else{
          
          upper.visible = true
        }
      }
      if(lower){
        if(this.controls.object.position.y < -245){
          lower.visible = false
        }
        else{
          lower.visible = true
        }
      }     
    }) 
 
  }
};
</script>
<style>
  body{
    margin:0px;
  }
    .progress{
        position: fixed;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .percent-text{
      font-family: 'Josefin Sans', sans-serif;
      color: white;
    }
</style>


